import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Info from './Info'
import AddFlight from './AddFlight'

export default function ControlPanel(props) {
  const handleShowAllFlights = () => {
    props.setFlag(false); //   AddFlight    " "
  }

  return (
    <div id='div'>
 <button onClick={props.toggleAllFlight} id='btn' className="btn btn-dark"> </button>
      <button onClick={props.toggleAddFlight} id='btn' className="btn btn-dark"> </button>
     

<button onClick={props.toggleSortlight} id='btn' className="btn btn-dark" > </button>

<button onClick={props.toggleDelFlight} id='btn' className="btn btn-dark" > </button>
<Link to={'/'}><button id='btn' onClick={props.toggleExit} className="btn btn-dark"></button></Link>




    </div>
  )
}
